import './main-canvas.css';

export default class MainCanvas {
  constructor() {
    this.canvas = null;
    this.ctx = null;
    this.ifMouseDown = false;
  }

  changeIfMouseDown() {
    this.ifMouseDown = true;
  }

  changeIfMouseUp() {
    this.ifMouseDown = false;
    this.ctx.beginPath();
  }

  painting(e) {
    if (this.ifMouseDown) {
      this.ctx.lineTo(e.offsetX, e.offsetY);
      this.ctx.stroke();

      this.ctx.beginPath();
      this.ctx.arc(e.offsetX, e.offsetY, 5, 0, Math.PI * 2);
      this.ctx.fill();

      this.ctx.beginPath();
      this.ctx.moveTo(e.offsetX, e.offsetY);
    }
  }

  addEvents() {
    const paintingBind = this.painting.bind(this);
    const changeIfMouseDownBind = this.changeIfMouseDown.bind(this);
    const changeIfMouseUpBind = this.changeIfMouseUp.bind(this);

    this.canvas.addEventListener('mousemove', paintingBind);
    this.canvas.addEventListener('mousedown', changeIfMouseDownBind);
    this.canvas.addEventListener('mouseup', changeIfMouseUpBind);
    this.canvas.addEventListener('mouseleave', changeIfMouseUpBind);
  }

  render() {
    const mainCanvas = document.getElementById('drawing-canvas-container');
    mainCanvas.innerHTML = `
    <div class="main-canvas-background"></div>
    <canvas id="drawing-canvas" class="drawing-canvas" width="500px" height="500px"></canvas>`;

    this.canvas = document.getElementById('drawing-canvas');
    this.ctx = this.canvas.getContext('2d');
    this.ctx.lineWidth = 10;

    this.addEvents();
  }
}
