import './animation-preview.css';

export default class AnimationPreview {
  constructor(frames) {
    this.animationPlayer = null;
    this.frames = frames;
    this.imgsArray = this.frames.imgsArray;
    this.intervalId = null;
    this.intervalCounter = 0;
    this.fps = 1000;
    this.rangeFps = null;
    this.animationPlayerContainer = null;
    this.fullScreenBtn = null;
  }

  startAnimation() {
    this.intervalId = setInterval(() => {
      if (this.imgsArray.length >= 1 && this.intervalCounter < this.imgsArray.length) {
        this.animationPlayer.style.backgroundImage = `url(${this.imgsArray[this.intervalCounter].src})`;
        this.intervalCounter += 1;
      }

      if (this.intervalCounter >= this.imgsArray.length) {
        if (this.imgsArray[this.intervalCounter - 1]) {
          this.animationPlayer.style.backgroundImage = `url(${this.imgsArray[this.intervalCounter - 1].src})`;
        }
        this.intervalCounter = 0;
      }
    }, this.fps);
  }

  redrawingOnAnimationPlayer() {
    if (this.intervalCounter === 0) {
      document.getElementById('animation-player').style.backgroundImage = `url(${this.frames.currentMainCanvasImg.src})`;
    }
  }

  changeFps() {
    this.fps = 1000 / this.rangeFps.value;
    const displayFps = document.getElementById('display-fps');

    displayFps.innerText = `${this.rangeFps.value} FPS`;

    if (this.fps === Infinity) {
      displayFps.innerText = `${this.rangeFps.value} FPS`;

      this.intervalCounter = 0;

      window.clearInterval(this.intervalId);
      this.intervalId = setInterval(() => {
        this.animationPlayer.style.backgroundImage = `url(${this.frames.currentMainCanvasImg.src})`;
      }, 100);

      this.redrawingOnAnimationPlayer();
      return;
    }
    window.clearInterval(this.intervalId);
    this.startAnimation();
  }

  launchToFullscreen() {
    this.animationPlayerContainer.webkitRequestFullscreen();
  }

  addListeners() {
    const redrawingOnAnimationPlayerWrapper = this.redrawingOnAnimationPlayer.bind(this);
    this.frames.mainCanvas.addEventListener('mouseup', redrawingOnAnimationPlayerWrapper);
    this.frames.mainCanvas.addEventListener('mouseleave', redrawingOnAnimationPlayerWrapper);

    const changeFpsWrapper = this.changeFps.bind(this);
    this.rangeFps.addEventListener('input', changeFpsWrapper);

    const launchToFullscreenWrapper = this.launchToFullscreen.bind(this);
    this.fullScreenBtn.addEventListener('click', launchToFullscreenWrapper);
  }

  render() {
    const animatedPreview = document.getElementById('animated-preview-container');
    animatedPreview.innerHTML = `
    <div id="canvas-container-wrapper" class="canvas-container-wrapper">
      <div id="animated-preview-canvas-container" class="animated-preview-canvas-container">
        <div class="canvas-background"></div>
        <div id="animation-player" class="animation-player"></div>
      </div>
      <div class="display-fps-container">
        <button id="full-screen-btn" class="full-screen-btn">Full</button>
        <span id="display-fps" class="display-fps">1 FPS</span>
        <input id="range-fps" class="range-fps" type="range" min="0" step="1" value="1" max="24">
      </div>
    </div>`;

    this.animationPlayer = document.getElementById('animation-player');
    this.rangeFps = document.getElementById('range-fps');
    this.animationPlayerContainer = document.getElementById('canvas-container-wrapper');
    this.fullScreenBtn = document.getElementById('full-screen-btn');

    this.addListeners();

    this.startAnimation();
  }
}
